.container{background-color:#000;color:#fff;min-height:100vh;padding-top:64px;padding-inline:1rem;display:flex;flex-direction:column;gap:3rem;overflow-x:hidden}.header{width:100%}.aboutSection{text-align:center;max-width:800px;margin:0 auto;display:flex;flex-direction:column;gap:1rem}.aboutSection h2{font-size:2rem;font-weight:600}.aboutSection p{font-size:1.1rem;color:#ccc}.skillSection{max-width:900px;margin:0 auto;display:flex;flex-direction:column;gap:1.5rem}.skillSection h3{font-size:1.5rem;font-weight:600}.skillSection p{color:#bbb}.skillSection .toolGrid{display:flex;flex-wrap:wrap;gap:1rem}.skillSection .toolGrid span{background:#fff;color:#000;padding:.5rem 1rem;border-radius:1.5rem;font-size:.9rem;font-weight:500}.processSection{display:flex;flex-direction:column;gap:5rem;padding-block:5rem;max-width:1000px;margin:0 auto}.step{opacity:0;transform:translateY(40px);will-change:transform,opacity}.step .stepNumber{font-size:1.8rem;font-weight:bold;color:#fff}.step .stepTitle{font-size:1.4rem;margin-top:.3rem}.step .stepDescription{margin-top:.8rem;color:#bbb;font-size:1.1rem;line-height:1.6;max-width:700px}